.ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    box-sizing: border-box;
    border: 1px solid #d4d9e3;
    padding: 0;
}
.li_title_container {
    display: flex;
    border-bottom: 1px solid #d4d9e3;;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
    height: 60px;
    padding-left: 30px;
    font-family: Arial, Helvetica, sans-serif;
}
.li_title {
    width: 50%;
    display: flex;
    justify-content: flex-start;
}
.li_activitys_number {
    width: 50%;
    display: flex;
    justify-content: flex-end;
}
.span_number_1 {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #5384ff;
    margin-right: 20px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    color: white;
}

.li_activity {
    height: var(--li_height);
    display: flex;
    align-items: center;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
}
.li_activity:hover {
    background-color: #e5eaf5;
}
.profile_icon {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    width: 75px;
}
.clock_icon {
    font-size: 20px;
    margin-right: 5px;
}

.activity_conteiner {
    padding-right: 10px;
    height: 50px;
}
.activity_username {
    font-weight: bold;
}
.activity_action {
    color: gray;
}
.activity_on_task {
    font-weight: bold;
}

.activity_time_conteiner {
    padding-top: 3px;
    display: flex;
    align-items: center;
}
